






























































































































import { Component, Prop, Vue } from "vue-property-decorator";
import jobApi from "@/api/jobApi";
import { JobModel } from "@/model/jobModel";

@Component
export default class JobList extends Vue {
  // hint
  hintJobTypeList: object[] = [
    { value : 1, label : "自定义 / Jar"}
  ];
  // Job Props
  jobListColumns: object[] = [
    {
      type: "expand",
      key: "id",
      width: 50
    },
    {
      type: "selection",
      title: "ID",
      key: "id",
      width: 50
    },
    {
      title: "ID",
      key: "id",
      align: "center",
      width: 100
    },
    {
      title: "名称",
      key: "name",
      align: "center"
    },
    {
      title: "类型",
      key: "type",
      align: "center"
    },
    {
      title: "描述",
      key: "description",
      align: "center"
    },
    {
      title: "开始时间",
      key: "start_time",
      align: "center"
    },
    {
      title: "结束时间",
      key: "stop_time",
      align: "center"
    },
    {
      title: "状态",
      key: "last_status",
      align: "center"
    },
    {
      title: "操作",
      fixed: "right",
      align: "center",
      slot: "operator",
      width: 140
    }
  ];
  jobList: JobModel[] = [];
  // Job Query
  page: any = {
    total: 100,
    pageNum: 1,
    pageSize: 10
  };
  jobQueryCondition: object = {
    id: "",
    name: ""
  };
  clickQuery() {
    this.getJobList();
  }
  pageChange(num: number) {
    this.page.pageNum = num;
    this.getJobList();
  }
  pageSizeChange(size: number) {
    this.page.pageSize = size;
    this.getJobList();
  }
  // Job Create
  jobCreateModal: boolean = false;
  jobCreateItems: JobModel = {
    name: "name",
    type: 1,
    description: "desc"
  };
  clickCreate() {
    this.jobCreateModal = true;
  }
  clickCreateOk() {
    jobApi
      .addJob(this.jobCreateItems)
      .then(res => {
        this.getJobList();
      })
      .then(res => {
        this.$Notice.success({
          title: "新建作业成功"
        });
      });
  }
  clickCreateCancel() {
    this.$Notice.warning({
      title: "取消新建作业"
    });
  }
  // Job Click Actions
  clickDetail(row: any) {
    this.$router.push({
      path: "/job/detail",
      query: {
        id: row.id
      }
    });
  }
  clickEdit(row: any) {
    this.$router.push({
      path: "/job/edit",
      query: {
        id: row.id
      }
    });
  }
  clickStart() {
    this.$Message.success("启动作业成功");
  }
  clickRestart() {
    this.$Message.success("重启作业成功");
  }
  clickStop() {
    this.$Message.success("停止作业成功");
  }
  clickDelete() {
    this.$Message.success("删除作业成功");
  }

  getJobList() {
    jobApi.queryJobs(this.page).then((res: any) => {
      this.jobList = res.list;
      this.page.total = res.total;
    });
  }

  mounted() {
    this.getJobList();
  }
}
